.users-page {
  padding-top: calc(7vh + 10px);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.users-page button {
  height: 2.5rem;
  margin: 1rem;
  width: 6rem;
  cursor: pointer;
  border-radius: 999px;
  font-size: 1.1em;
  background: #9092bc;
  border: none;
  color: #000;
  font-weight: 500;
  transition: 0.2s;
}
.users-page button:hover {
  background: #42446a;
  color: white;
}
.users-page button:active {
  opacity: 0.8;
}

#profile-picture {
  margin: 1rem;
  width: 15vw;
  height: 15vw;
  position: relative;
  background: black;
  border-radius: 50%;
  border: 5px solid #42446a;
  background-position: 0px -15px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
#profile-picture::before {
  content: "";
  background: var(--main-url);
  position: absolute;
  top: 0px;
  bottom: 0px;
  right: 0px;
  left: 0px;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50%;
}
#profile-picture div {
  z-index: 2;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  transform: scale(0.5);
  opacity: 0;
}
#profile-picture div input {
  display: none;
}
#profile-picture div label {
  height: 100%;
  width: 100%;
}
#profile-picture:hover div {
  opacity: 1;
}
#profile-picture:hover::before {
  opacity: 0.6;
}

#firstlast-name {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 1rem 0;
  justify-content: space-between;
  width: 50vw;
}
#firstlast-name div label,
#firstlast-name input {
  display: block;
  margin: 0.3rem;
}

#email-add {
  width: 50vw;
  margin: 1rem 0;
}
#email-add label,
#email-add input {
  display: block;
  margin: 0.3rem;
}
#email-add input {
  width: 100%;
}

#contact-number {
  width: 50vw;
  margin: 1rem 0;
}
#contact-number label,
#contact-number input {
  display: block;
  margin: 0.3rem;
}

