.navbar {
  height: 50px;
  background: #9092bc;
  width: 100vw;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: fixed;
  z-index: 100;
}
.navbar #hamburger {
  height: 100%;
  width: 30vw;
  margin-left: 1.2em;
  align-self: center;
  cursor: pointer;
}

#search-icon {
  height: 100%;
  position: absolute;
  top: 0px;
  left: 90%;
  cursor: pointer;
}

.searchbar {
  width: 35vw;
  height: 2em;
  position: relative;
  justify-self: center;
  align-self: center;
}

#search-input {
  width: 100%;
  height: 100%;
  border-radius: 999px;
  padding-left: 10px;
  outline-style: none;
  border: none;
  font-size: 1rem;
  color: #42446a;
}

.upload-icon {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  cursor: pointer;
  margin: 5px 20px 5px 5px;
  width: 30vw;
}

